.banner
	min-height: 23.3rem 
	overflow: hidden
	+cover
	position: relative
	+r($xs)
		min-height: 12.5rem
	&:after
		content: ""
		position: absolute
		top: 0
		bottom: 0
		right: 0
		left: 0
		background: rgba(#000,.5)  
	img
		width: 100%
		height: auto
	&__in
		position: relative
		min-height: 23.3rem
		+r($xs)
			min-height: 12.5rem 
		+clr
	&_big
		min-height: 48.6rem 
		.banner__in
			min-height: 48.6rem
		&:after
			display: none
	&_small
		min-height: 17rem 
		+r($xs)
			min-height: 14.3rem  
		.banner__in
			min-height: 17rem
			+r($xs)
				min-height: 14.3rem  
			
.article
	+clr
	h1
		color: $title
		font-size: 3.5rem
		text-transform: uppercase 
		line-height: 1.2 
		margin-bottom: 2.2rem
		+med
		+r($sm)
			font-size: 3rem
		+r($xs)
			font-size: 2.5rem
			margin-bottom: 1rem
		+r($xxs)
			font-size: 2rem
	img
		float: left
		width: 48rem
		height: auto
		margin: 0 2rem 1.5rem 0 
		+r($sm)
			width: 40rem
			margin: 0 1.5rem 1rem 0 
		+r($xs)
			float: none
			width: 100%
	p
		margin-bottom: 2.3rem
		+r($sm)
			margin-bottom: 1.8rem
.share-panel
	border-top: 1px solid $footer
	padding: 1rem 0
	font-size: 0 
	margin-top: 0.5rem
	+r($xs)
		padding: 2.2rem 0 
	+r($xxs)
		padding: 1rem 0

.share
	display: inline-block
	vertical-align: middle
	height: 2.9rem
	line-height: 2.9rem
	transition: background-color .3s
	margin-right: 1rem
	+r($xs)
		height: 2rem
		line-height: 2rem 
	+r($xxs)
		display: block
		max-width: 11.5rem 
		margin: 1rem auto
		
	&:last-child
		margin-right: 0
		+r($xxs)
			margin-right: auto 
	&.is-fb
		background: $fb
		&:hover
			background-color: darken($fb, 10%)
		&:active
			background-color: lighten($fb, 5%) 
	&.is-tw
		background: $tw
		&:hover
			background-color: darken($tw, 10%)
		&:active
			background-color: lighten($tw, 5%) 
	&.is-gp
		background: $gp
		&:hover
			background-color: darken($gp, 10%)
		&:active
			background-color: lighten($gp, 5%) 
	&__net
		width: 2.9rem
		border-right: 1px solid white
		text-align: center
		display: inline-block
		vertical-align: middle
		font-size: 0
		+r($xs)
			width: 2rem
		i
			vertical-align: middle
			+icon
			color: white
	&__text
		padding: 0 1rem 
		color: white 
		display: inline-block
		vertical-align: middle
		font-size: 1.3rem
		text-align: center
		+r($xs)
			font-size: .9rem
			padding: 0 .4rem 
	.icon-fb
		font-size: 1.4rem
		+r($xs)
			font-size: 1.1rem
	.icon-tw
		font-size: 1.3rem
		+r($xs)
			font-size: 0.9rem
	.icon-gp
		font-size: 1.3rem 
		+r($xs)
			font-size: 1rem
.breadcrumbs
	margin: 3.5rem 0 
	+r($xs)
		margin: 1.5rem 0 
	&__item
		display: inline-block
		vertical-align: middle
		&:after
			content: "/"
			display: inline-block
			vertical-align: middle
			color: #6a6d73
			margin: 0 1rem 
			+r($xs)
				margin: 0 .6rem 
		span
			line-height: 1
			color: $title
			border-bottom: 1px solid $title
			transition: border-color .3s, color .3s
			+r($xs)
				font-size: 1rem
		&:last-child
			&:after
				display: none			
		&:hover
			span
				border-color: transparent
		&:active
			span
				border-color: $title
		&.is-active
			cursor: default
			span
				border: none
				color: #6a6d73 
.promo
	width: 100% 
	min-height: 48.6rem 
	background: rgba(#000,.8) 
	padding: 14.6rem 5.3rem 4.5rem
	color: white
	&__dib
		display: inline-block
		vertical-align: bottom  
		max-width: 22rem 
		margin-right: 6rem
		&:last-child
			margin-right: 0
	&__name
		color: white
		font-size: 8rem
		margin-bottom: -1.6rem
		+med 
	&__list-name
		+bold
		font-size: 2rem
	&__list
		li
			font-size: 1.5rem
			margin-top: 0.9rem
			+reg
			&:before
				content: "-"
				display: inline-block
				margin-right: .5rem 
.price
	font-size: 3.5rem
	color: white
	+med
	span
		display: block
		font-size: 1.5rem
		margin-bottom: -1rem 
.navigation
	display: table
	width: 100%
	li
		display: table-cell 
		white-space: nowrap
		height: 4.5rem 
		background: $footer
		border-left: 1px solid white 
		line-height: 4.5rem
		text-align: center
		&:first-child	
			background: $hot
			border-left: none
			a
				color: white
				&:before
					content: ""
					position: absolute
					height: 4.5rem
					width: 60rem
					background-color: $hot
					left: -60rem  
					transition: background-color .3s  
				&:hover
					background: $hot-hover 
					&:before
						background-color: $hot-hover 
		&:last-child
			background: $btn-dark 
			a
				color: white
				&:before
					content: ""
					position: absolute
					height: 4.5rem
					width: 60rem
					background-color: $btn-dark
					right: -60rem  
					transition: background-color .3s 
				&:hover
					background: lighten($btn-dark,10%) 
					&:before
						background-color: lighten($btn-dark,10%) 
	a
		display: block	
		position: relative	
		height: 100%
		color: $title
		background-color: transparent
		transition: background-color .3s 
		+reg
		&:hover
			background-color: darken($footer, 10%) 
	&_default
		li
			&:first-child,
			&:last-child
				background: $footer
				a
					color: $title
					&:before,
					&:after
						background: $footer
					&:hover
						background: darken($footer,10%)  
						&:before,
						&:after
							background: darken($footer,10%)
			&.is-active
				background: #f1f3f7
				a
					cursor: default
					&:before,
					&:after
						background: #f1f3f7 
					&:hover	
						background: #f1f3f7 
						&:before,
						&:after
							background: #f1f3f7 
.tabs
	font-size: 0
	&__item
		display: inline-block
		height: 2.7rem
		line-height: 2.7rem 
		padding: 0 2rem
		font-size: 1.2rem
		color: $title
		background: $footer 
		text-transform: uppercase
		margin-right: 1rem
		transition: background-color .3s
		+light
		&:hover
			background-color: darken($footer,10%) 
		&.is-active
			background-color: $btn
			position: relative
			color: white
			&:after
				content: ""
				position: absolute
				bottom: -.3rem 
				left: 50%
				margin-left: -.5rem 
				+arr(1,.4,$btn,b)
		&:last-child
			margin-right: 0 
.tabs-image
	&__item
		width: 100%
		img
			width: 100%
			height: auto
.crsl-demo
	margin-left: 3rem
	&__slide
		background: $footer
		padding-bottom: 3.8rem 
	&__img
		width: 100%
		height: 21.5rem
		overflow: hidden
		img
			height: auto
			width: 100%
	&__bottom	
		height: 13.5rem
		padding: 1rem 2rem 
	&__title
		+light
		font-size: 2rem
		color: $title  
		text-transform: uppercase 
.link-name
	+reg
	font-size: 1.6rem
	color: $title
	border-bottom: 1px solid transparent
	display: inline-block
	transition: border-color .3s
	line-height: 1
	+r($xs)
		border-color: $title
	&:hover
		border-color: $title	
		color: $title
	&:active
		border-color: $title
		+r($xs)
			border-color: transparent
	&_big
		font-size: 2.4rem
		+light	
		text-transform: uppercase
		+r($xs)
			font-size: 2.2rem 
		+r($xxs)
			font-size: 1.8rem 
	&_light
		+light
		font-size: 1.8rem
		text-transform: uppercase 
		+r($xs)
			font-size: 1.7rem
			+med 
		+r($xxs)
			font-size: 1.4rem 
	&.with-mb
		margin-bottom: 2.5rem
.error
	padding: 2rem 0 2.5rem 
	+r($xs)
		padding: 0 0 2rem
	&__number
		color: $btn
		font-size: 6rem
		+med
		+r($xs)
			font-size: 10rem
			display: block
			margin-top: -1.5rem
		+r($xxs)
			font-size: 6rem
			margin-top: 0
	&__info 
		font-size: 6rem
		+med
		color: $title
		text-transform: uppercase 
		+r($xs)
			font-size: 3rem
			display: block
			margin-top: -2rem
		+r($xxs)
			font-size: 2.5rem
	p
		margin-top: -2rem
		+r($xs)
			margin-top: 0
.photo
	+r($xs)
		margin-bottom: 4rem 
	&__img
		width: 20rem
		height: 23rem
		overflow: hidden
		+r($xs)
			display: inline-block
			width: 16.8rem
			height: 19.5rem
		img
			height: 100%
			width: auto
	&__name
		color: #46484C 
		font-size: 2rem
		margin: .8rem 0 -.5rem 
		+r($xs)
			font-size: 1.8rem
	&__right
		+r($xs)
			display: inline-block
.quote
	position: relative
	p
		+italic
		color: $text 
		margin-top: 2.5rem
		&:first-child
			margin-top: 0
	&:before,
	&:after
		content: ""
		position: absolute
		top: .5rem  
		width: .9rem
		height: 3.1rem
		background: $menu
		transform: rotate(7deg) skew(-7deg,-7deg)
		+r($xs)
			top: -2rem
			height: 1.5rem
			width: .5rem 
	&:before
		left: -2.5rem
		+r($xs)
			left: 1.5rem 
	&:after
		left: -4rem
		+r($xs)
			left: 0.5rem
.img
	width: 100%
	+r($xs)
		margin-top: 1.8rem
	img
		width: 100%
		height: auto 
.item
	height: 20rem
	width: 100%
	overflow: hidden
	position: relative
	display: block
	+r($xs)
		margin-bottom: 2rem
		height: 18.4rem
	&:hover
		&:before
			opacity: .4 
	&:active
		&:before
			opacity: 0.2
	&:before
		content: ""
		position: absolute
		top: 0
		bottom: 0
		right: 0
		left: 0
		background: $item
		opacity: 0.8
		transition: opacity .3s
	&__img
		+cover
		width: 100%
		img
			width: 100%
			height: auto
	&__name
		position: absolute
		bottom: 1.7rem
		right: 1rem
		left: 1rem
		text-align: center 
		color: white
		font-size: 2.4rem
		line-height: 1.2
		+med
		text-transform: uppercase
		+r($sm)
			font-size: 2.2rem
.border-out
	position: relative
	padding: 1.2rem 0 3.3rem 
	margin-top: 2.3rem
	+r($xs)
		padding: 0
		margin-top: 0
		margin-bottom: 2rem  
	&:before
		content: ""
		position: absolute
		width: 90rem
		height: 1px
		right: 0
		top: 0 
		background: $footer 
		+r($xs)
			display: none
.map
	position: relative
	height: 62.2rem
	&__in
		position: absolute
		padding: 4.5rem 3rem 
		top: 0
		bottom: 0
		right: 0
		left: 66.6%
		background: #2B2B2B 
		+r($xs)
			position: static
			width: 100%
			padding: 2.5rem 0 

.page
	position: relative
	+clr
	margin-bottom: 4.5rem
	&:last-child
		margin-bottom: 0
	.name
		margin-top: 0
	&__img
		position: absolute
		right: 0
		left: 51%
		bottom: 0
		top: 0
		overflow: hidden
		img
			width: 100%
			height: auto 
		+r($xs)
			display: block
			position: static
			clear: both
			height: 14.25rem
	.text
		+r($xs)
			margin-bottom: 2rem
	&_secondary
		.page__img
			left: 0
			right: 51%
.philos-wrap
	+clr
	margin-top: 2.6rem
	.text
		margin-left: -4.5rem
		+r($md)
			margin-left: 0
.philos
	background: #375396
	+clr
	width: 63.4rem
	position: relative
	+r($md)
		width: 56.3rem
	+r($sm)
		width: 34.5rem
	+r($xs)
		margin-top: 1.5rem
	+r($xxs)
		width: 31rem
	&__item
		width: 18.7rem
		height: 13rem
		padding: 0 0 0 3rem
		position: relative
		+vertical
		+r($md)
			height: 11rem
			width: 16.7rem
		+r($sm)
			height: 7rem
			width: 10rem
			padding: 0 0 0 1rem 
		+r($xxs)
			height: 6rem
			width: 10.5rem 
		&:after
			content: ""
			position: absolute
			right: 0
			top: 0
		&.is-1
			background: #009ed6 
			padding: 0 3rem 0 2rem 
			+r($sm)
				padding: 0 0 0 1rem
			&:after
				right: -13rem
				+arr(13,13,#009ed6,tl)
				+r($md)
					right: -11rem
					+arr(11,11,#009ed6,tl)
				+r($sm)
					right: -7rem
					+arr(7,7,#009ed6,tl)
				+r($xxs)
					right: -5rem
					+arr(5,6,#009ed6,tl)
		&.is-2
			background: #E46C0A 
			&:after
				right: -13rem
				+arr(13,13,#E46C0A ,bl)
				+r($md)
					right: -11rem
					+arr(11,11,#E46C0A ,bl)
				+r($sm)
					right: -7rem
					+arr(7,7,#E46C0A ,bl)
				+r($xxs)
					right: -5rem
					+arr(5,6,#E46C0A ,bl)
		&.is-3
			background: #cf2e2e 
			text-align: right
			padding: 0 3rem 0 0
			+r($sm)
				padding: 0 1rem 0 0 
			&:after
				left: -13rem
				+arr(13,13,#cf2e2e ,tr)
				+r($md)
					left: -11rem
					+arr(11,11,#cf2e2e ,tr)
				+r($sm)
					left: -7rem
					+arr(7,7,#cf2e2e ,tr)
				+r($xxs)
					left: -5rem
					+arr(5,6,#cf2e2e ,tr)
		&.is-4
			background: #92d050 
			text-align: right
			padding: 0 3rem 0 2rem
			+r($sm)
				padding: 0 1rem 0 0 
			&:after
				left: -13rem
				+arr(13,13,#92d050,br)
				+r($md)
					left: -11rem
					+arr(11,11,#92d050,br)
				+r($sm)
					left: -7rem
					+arr(7,7,#92d050,br)
				+r($xxs)
					left: -5rem
					+arr(5,6,#92d050,br)
	&__left
		float: left
	&__right
		float: right
	&__name
		position: absolute
		z-index: 10
		width: 14rem
		left: 50%
		top: 50%
		height: 5rem
		margin-top: -2.5rem
		margin-left: -7rem 
		text-align: center
		+r($sm)
			width: 9.5rem
			margin-top: -1.5rem
			margin-left: -4.8rem 
		
	.name
		color: white
		margin: 0 0
		display: inline-block
		vertical-align: middle
		line-height: 1.35
		font-size: 1.5rem
		+r($sm)
			line-height: 1.2
			+light 
		+r($xxs)
			font-size: 1.1rem
	&__text
		+light
		font-size: 1.5rem
		display: inline-block
		color: white 
		line-height: 1.3
		vertical-align: middle
		letter-spacing: 0
		+r($sm)
			font-size: 1rem
.diagram
