		height: 5.6rem
		font-size: 1.5rem 
		color: #ACACAC 
		padding: 0 7.6rem 0 2rem 
		background: #f0f2f5 
		+italic
		+r($sm)
			font-size: 1.2rem
			padding: 0 4.5rem 0 1.6rem 
			height: 3.5rem
.tab-in
	float: right
	text-align: right
	padding-right: 3.3rem 
	font-size: 0 
	+r($sm)
		float: none
		padding-right: 0
	+r($xs)
		text-align: left
	&__item
		display: inline-block
		margin-left: 3.3rem
		vertical-align: middle
		position: relative
		+r($sm)
			margin-left: 3rem
		&:first-child
