		&:active
			span
				border-color: transparent
		span
			display: inline-block
			vertical-align: middle
			margin-left: 1rem 
			color: #727580
			font-size: 1.5rem 
			border-bottom: 1px solid transparent
			line-height: 1
			transition: border-color .3s
			+r($sm)
				font-size: 1.7rem
		i
			vertical-align: middle
			+icon
			color: #727580
			transition: color .3s
		.icon-loc2
			font-size: 1.6rem
			+r($sm)
				font-size: 1.85rem
		.icon-list
			font-size: 1.1rem	
			+r($sm)
				font-size: 1.25rem		
		&.is-active
			span
				color: #252629 
			i
				color: $btn 
			&:hover
				cursor: default
				span
					border-color: transparent
.card
	background: $footer
	width: 100% 
	margin-top: 2rem
	&__body
		padding: 3rem 5rem 2.4rem
		+r($md)
			padding: 2.2rem 3rem 2rem 4rem
	&__title
		font-size: 2.4rem
		color: $title
		+light
		position: relative
		text-transform: uppercase
		letter-spacing: 0
		line-height: 1
		+r($md)
			font-size: 2.3rem
		+r($sm)
			font-size: 2.2rem
		+r($xxs)
			font-size: 1.8rem
			margin-bottom: 1rem
		&:before
			content: $icon-loc2
			+icon
			font-size: 3rem 
			color: $btn
			position: absolute 
			left: -3.3rem 
			top: 50%
			margin-top: -1.5rem 
			+r($md)
				top: 0
				margin-top: 0
				font-size: 2.1rem
				left: -2.3rem
	&__name
		font-size: 1.5rem
		color: $title
		+r($sm)
			font-size: 1.2rem
	&__list
		li
			font-size: 1.5rem
			line-height: 1.7
			+r($sm)
				font-size: 1.2rem
		&.is-modif
			li				 
				&:before
					content: "-"
					display: inline-block
					vertical-align: middle
					margin-right: .2rem
	&__link
		border-bottom: 1px solid $btn
		color: $btn
		line-height: 1
		font-size: 1.5rem
		margin: 1rem 0 
		display: inline-block
		transition: border-color .3s, color .3s
		+r($sm)
			font-size: 1.2rem
		&:hover
			border-color: transparent
			color: $btn
		&:active
			border-color: darken($btn,10%)
			color: darken($btn,10%)
	&__footer
		border-top: 1px solid white
		padding: 1.3rem 0 1rem
		text-align: center
		+r($sm)
			text-align: left 
			padding-left: 2.7rem 
.link-btn
	text-transform: uppercase
	display: inline-block
	+reg
	font-size: 1.5rem
	color: $title 
	+r($sm)
		+light
	span
		display: inline-block
		color: $btn
		font-size: 1.5rem
		vertical-align: middle 
		margin-left: .5rem 
.input
	+clr
	margin-bottom: 2rem
	&__title
		float: left
		width: 12rem
		margin-right: 2rem 
		line-height: 1.4rem
		margin-top: 1rem
		span
			display: inline-block
			vertical-align: middle
			font-size: 1.3rem
		sub
			color: $hot 
			vertical-align: top
			font-size: 1.3rem
		
	&__in
		overflow: hidden
		input
			width: 100%
			height: 4.3rem
			border: 1px solid $item
			padding: 0 1.5rem
			color: $title  
		textarea
			width: 100%
			height: 10.6rem 
			border: 1px solid $item
			padding: 1rem 1.5rem
			color: $title
			resize: none 
	&_secondary
		.input__in
			+clr			
			&.is-1
				float: left
				margin-right: 1rem
				width: 15.2rem
			&.is-2
				overflow: hidden
	&_checkbox
		input
			width: auto
.checkbox
	position: relative
	display: inline-block
	cursor: pointer
	margin-right: 2.9rem
	&__text
		display: block
		position: relative
		padding-left: 2.8rem
		color: $text
		transition: color .3s
		+nosel
		&:hover
			color: $btn
			i
				border-color: $btn
		i
			position: absolute
			width: 1.8rem
			height: 1.8rem
			left: 0
			margin-top: -0.9rem
			top: 50%
			border-radius: 50%
			border: 2px solid $item
			transition: border-color .3s
			&:before
				display: none
				content: ""
				position: absolute
				left: 50%
				top: 50%
				margin: -.3rem 0 0 -.3rem
				width: .6rem
				height: .6rem
				border-radius: 50%
				background: $btn
	input
		position: absolute
		opacity: 0
		&:checked
			+ span
				i
					border-color: $btn
					&:before
						display: block
.form-wrap
	padding-left: 1.5rem 
	&.is-1
		padding-left: 0
		padding-right: 1.5rem 
	



		
		 


