// # SHORT HOWTO
// ## Sprites
//     .whatever
// 			+s(codedby) 
// ## gradient example
//     	+g(#232323, #5c5c5c);
//	   horizontal
//     	+gh(#232323, #5c5c5c);
// ## making bw-images
//     img
//			@extend %gray
// ## predefined media-queries: https://github.com/paranoida/sass-mediaqueries
//     +r(.video320)
//			display: none
// +max-screen(1000), +iphone5, +iphone4, +ipad-retina, +ipad, +hdpi - also available
//
// ## use cycles
//     @for $i from 1 through 3
//       .item-#{$i}
//			width: 2em * $i;
//     @each $s in a,b,c
// 	    	#{$s}
//				display: none
// ## fonts
// Save fonts to /src/fonts
// +font(book, cytiapro-black-webfont)
// and create mixin
// =book
// 	font-family: "book", Arial, sans-serif
html
	font-size: 62.5%
body
	font-family: Tahoma, sans-serif
	font-size: 1.3rem
	line-height: 1.8
	letter-spacing: .5px
	color: $text
	+r($xs)
		font-size: 1.1rem
		line-height: 1.5 
	&.is-open-menu
		.main
			transform: translateX(32rem)
		.menu-mob
			transform: translateX(0)
a
	&:hover
		color: inherit
	&:active
		color: inherit
	&:focus
		color: inherit 

.out
	overflow: hidden
	position: relative
.main
	transition: transform .3s
.container-fluid
	max-width: 120rem	
.xxs-12
	+r(321)
		width: 100%
.col-xs-1,
.col-xs-2,
.col-xs-3,
.col-xs-4,
.col-xs-5,
.col-xs-6,
.col-xs-7,
.col-xs-8,
.col-xs-9,
.col-xs-10,
.col-xs-11,
.col-xs-12,
.col-sm-1,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-sm-10,
.col-sm-11,
.col-sm-12
	padding: 0 .5rem 
// common blocks
.hover-page
	width: 1200px
	margin: 50px auto 
.wrap-center
	text-align: center
.clr
	+clr
button
	vertical-align: middle
.btn
	background: $btn
	color: white
	display: inline-block
	padding: 0 4rem
	height: 3.7rem
	line-height: 3.7rem
	+reg
	text-transform: uppercase   
	transition: background-color .3s, border-color .3s, color .3s
	&:hover
		background-color: $btn-hover
	&:active
		background-color: darken($btn, 7%) 
	&_secondary
		border: 1px solid $btn
		background-color: transparent 
		&:hover
			border-color: white
			background-color: transparent 
		&:active
			border-color: $btn
			color: $btn
			background-color: transparent
	&_default
		&:hover
			background-color: white
			color: $block-dark
		&:active	
			color: white
			background-color: $btn  
	&_send
		background-color: transparent
		border: 1px solid white
		color: white
		height: 4.7rem
		line-height: 4.7rem
		font-size: 1.6rem
		padding: 0 3rem 
		+r($xs)
			padding: 0 1.7rem 
			font-size: 1.15rem
			height: 2.85rem
			line-height: 2.85rem
		&:hover
			background-color: white
			color: $block-dark
		&:active
			background-color: transparent
			color: white 
.btn-action
	background-color: transparent
	display: inline-block
	border: 1px solid $btn
	padding: 0 1.6rem 0 1.8rem
	height: 37px
	line-height: 3.7rem  
	transition: background-color .3s, color .3s, border-color .3s
	color: #41444a
	text-transform: uppercase 
	margin-top: 2.5rem
	+reg
	+r($sm)
		height: 2.35rem
		line-height: 2.35rem 
		padding: 0 1.9rem
		font-size: 1.1rem 
		margin-top: 1rem 
	span
		color: $btn
		display: inline-block
		margin-left: 3.2rem 
		font-size: 1.7rem
		font-family: Arial  
		+tr(color .3s) 
		+r($sm)
			font-size: 1.1rem
			margin-left: .5rem

	&:hover
		background-color: $btn 
		color: white 
		span
			color: white
	&:active
		background-color: darken($btn, 7%) 
		border-color: darken($btn, 7%)
		color: white 
		span
			color: white
	&_hot
		background-color: $hot
		height: 3.8rem
		line-height: 3.8rem 
		color: white
		border: none
		margin-top: 0
		span
			color: white 
		&:hover
			background-color: $hot-hover
		&:active
			background-color: lighten($hot-hover, 5%)
	&_secondary
		border: none
		background: white
		color: $btn
	&_ondark
		color: white
		span
			margin-left: 2rem
			font-family: Arial  
			+r($sm)
				font-size: 1.1rem
				margin-left: .5rem
	&.is-short
		span
			margin-left: 1.2rem 
		

.btn-download
	height: 4.5rem
	line-height: 4.5rem
	padding: 0 4.2rem
	+reg
	color: $white
	background: $btn-dark
	+tr(background-color .3s) 
	&:hover
		background-color: $btn
	&:active
		background-color: darken($btn, 7%) 
.btn-nav
	display: inline-block
	background: $btn 
	height: 3.7rem
	width: 3.7rem
	line-height: 3.7rem
	+tr(background-color .3s)
	color: white 
	font-size: 0
	margin: 0 .9rem 
	position: relative
	&:before
		content: $icon-arr
		position: absolute
		+icon
		color: white 
		font-size: 1.1rem 
		top: 50%
		left: 50%
		margin-top: -.55rem 
		margin-left: -0.3rem
		+tr(color .3s)
	&.is-prev
		&:before
			transform: rotate(180deg)
	&:hover
		background-color: $btn-nav
	&:active
		background-color: lighten($btn-nav, 7%) 
	&_secondary
		background: white
		margin: 0 .7rem 
		&:before
			color: $arr
		&:hover
			background-color: white 
			&:before
				color: $btn 
		&:active
			background-color: white
			&:before
				color: $arr 
.slick-elements
	background: $block
	padding: 2rem 
	margin: 2rem 0 
.slick-dots
	display: inline-block
	font-size: 0
	margin: 0 4.4rem 
	vertical-align: middle
	li
		display: inline-block
		vertical-align: middle
		padding: 0 .4rem
		&:hover
			button
				background-color: #7281a1
		&:active
			button
				background-color: darken(#7281a1, 7%) 
	button
		height: 1rem
		width: 1rem  
		border-radius: 50%
		background: white
		transition: background-color .3s
.slick-active
	button
		background: $btn 
	&:hover
		button
			cursor: default 
			background-color: $btn !important
	&:active
		button
			cursor: default 
			background-color: $btn 
.slick-prev,
.slick-next
	display: inline-block
	vertical-align: middle
	width: 3.7rem
	height: 3.7rem
	background: white
	position: relative
	transition: background-color .3s
	+hidetext
	position: absolute
	z-index: 100
	bottom: 0
	right: 0
	&:before
		content: $icon-arr
		+icon
		position: absolute
		font-size: 1.1rem
		color: $arr
		transition: color .3s 
		left: 50%
		top: 50%
		margin-left: -.3rem 
		margin-top: -0.55rem 
	&:hover
		background-color: $btn 
		&:before
			color: white
	&:active
		background-color: darken($btn,7%)
		&:before
			color: white 
.slick-prev
	left: 0
	right: auto
	&:before
		transform: rotate(180deg)

.block
	padding: 4.5rem 0 5rem
	position: relative
	+r($sm)
		padding: 2rem 0 2.5rem 
	&_dark
		background: $block-dark
		padding: 4.5rem 0 5rem
		overflow: hidden
		position: relative
		+r($sm)
			padding: 2rem 0 2.5rem 
	&_secondary
		overflow: hidden
		background: $btn
		.block_part
			padding: 4.5rem 0
		.poster
			width: 41.6%
			height: 47.4rem
			+r($xs)
				display: none
	&_light
		background: $block-light
		padding: 3rem 0 
		+r($xs)
			padding: 2.5rem 0 .5rem 
		&.with-dif-pad
			padding: 4.5rem 0 5rem
			+r($xs)
				padding: 2.3rem 0 1.8rem 
		&.with-border
			border-top: 1px solid white
			padding: 4.5rem 0 5rem
			+r($xs)
				padding: 0 0 1.2rem
				border: none 

	&_no-pad
		padding: 0 
	&_index
		min-height: 5.4rem
		padding-right: 4rem
		+r($md)
			height: 50rem
		+r($sm)
			height: 44rem 
	&_border
		border-top: 1px solid $footer
		padding-bottom: 0
		padding-top: 2.5rem  
	&_different
		padding: 0
		+r($xs)
			margin-top: 2.2rem
		.block
			min-height: 45.2rem 
			padding-left: 2rem 
			+r($md)
				min-height: 35.2rem 
			+r($xs)
				min-height: auto 
				padding-left: 0
		.name
			color: white
			&.with-mb
				+r($sm)
					margin-bottom: 1.8rem  
		.poster
			left: 0
			right: 50%
			overflow: hidden
			+r($xs)
				position: static
				display: block
				height: 20.5rem
			img
				+r($sm)
					height: 100%
					width: auto
				+r($xs)
					height: auto
					width: 100%
			
.btn-app
	display: inline-block
	vertical-align: middle
	background-color: transparent 
	min-width: 33.5rem
	text-align: center
	padding: 0 1rem
	height: 4.3rem
	line-height: 4.3rem	
	border: 1px solid #231f20
	transition: background-color .3s, opacity .3s
	&:hover
		background-color: #231f20
		i,
		span
			color: white
	&:active	
		opacity: .8 
		background-color: #231f20
		i,
		span
			color: white
			 
	i,
	span
		display: inline-block
		vertical-align: middle
		transition: color .3s 
	span
		margin-left: 1.2rem 
		+reg
		color: $title
	i
		+icon
		 
	&.is-android
		border-color: #9cba30
		i
			color: #9cba30 
		&:hover
			background-color: #9cba30
			i
				color: white 
		&:active
			background-color: #9cba30
			opacity: 0.8
			i
				color: white

  	 
.title
	+med
	text-transform: uppercase 
	color: $title
	font-size: 3rem 
	margin-bottom: 2.5rem 
	line-height: 1
	+r($sm)
		font-size: 2.5rem
		margin-bottom: 1rem 
	+r($xxs)
		font-size: 2rem
	&_secondary
		color: white 
	&_page
		font-size: 3.5rem
		color: white  
		position: absolute
		z-index: 10
		bottom: 0
		+r($sm)
			font-size: 3rem
			margin-bottom: 2.5rem
		+r($xxs)
			font-size: 2.5rem
	&_hot
		font-size: 3.5rem
		color: $hot 
	&_mt
		margin-top: 4.5rem 
	&_marked
		width: 100%
		background: $block-dark
		padding: 1.8rem 2rem 1.6rem
		+bold
		font-size: 1.8rem
		color: white
		text-transform: uppercase 

		
.input-sub
	margin-top: -1rem
	display: inline-block
	vertical-align: bottom
	width: 100%
	margin-top: 1.4rem 
	+r($xs)
		// width: 100%
		// display: block
		margin-top: 1rem 
		margin-bottom: 1.4rem
	input
		width: 100%
		padding-bottom: .6rem
		border-bottom: 1px solid white
		background-color: transparent 
		color: white
		vertical-align: bottom 
		+reg 
		font-size: 1.5rem
		font-style: italic 
		+placeholder
			color: white 
.wrap-btnsend
	.btn
		margin-top: 0
	+r($md)
		text-align: right
	+r($xs)
		text-align: left
		margin-top: 1rem
		