.crsl-overview
	text-align: center
	visibility: hidden 
	opacity: 0
	+r($sm)
		margin-top: 1.5rem
	&.is-ready
		visibility: visible
		opacity: 1 
	&__img
		height: 45rem
		width: auto 
		text-align: center
		display: inline-block
		margin-bottom: -0.7rem
		+r($sm)
			height: 30rem 
		+r($xs)
			height: 22.9rem
		img
			height: 100%
			width: auto
.crsl-overview-nav
	visibility: hidden 
	opacity: 0
	&.is-ready
		visibility: visible
		opacity: 1
	&__img
		height: 8.6rem 
		width: 13.5rem
		overflow: hidden
		position: relative
		cursor: pointer
		+r($md)
			width: auto
		+r($xs)
			height: 4.3rem
		&:after
			content: ""
			position: absolute
			top: 0
			right: 0
			left: 0
			bottom: 0 
			transition: opacity .3s
			opacity: 0
			visibility: hidden
			background: $btn
		&:before
			content: $icon-plus
			font-size: 1.8rem
			color: transparent
			position: absolute
			+icon
			z-index: 2
			top: 50%
			left: 50%
			margin-top: -.9rem
			margin-left: -.9rem 
			transition: color .3s
			+r($xs)
				font-size: 0.9rem
				margin: -.45rem 0 0 -.45rem 
			
		img
			width: 100%
			height: auto  
	.slick-current
		.crsl-overview-nav__img
			&:after
				opacity: .9
				visibility: visible  
			&:before
				color: white 
	.slick-next,
	.slick-prev
		display: none !important
.crsl-history
	background: $block-dark
	visibility: hidden 
	opacity: 0
	&.is-ready
		visibility: visible
		opacity: 1
	&__slide
		position: relative
		height: 49rem 
		+r($md)
			height: 42rem
	&__item
		width: 41%
		padding: 4.5rem 0 5rem  
		.name
			color: white 
			margin: 0 0 2.2rem 0
		.title
			margin-bottom: 1.9rem
			+r($md)
				margin-bottom: 0
	&__l
		max-width: 120rem
		margin: 0 auto 
		+r($md)
			max-width: 98rem  
	&__img
		right: 0
		position: absolute
		top: 0
		bottom: 0
		overflow: hidden
		left: 42%
		+r($md)
			right: -10%
		img
			width: 100%
			height: auto
			// +r($md)
			// 	height: 100%
			// 	width: auto
	.slick-prev,
	.slick-next
		bottom: 5rem 
		left: 10%
		right: auto	
		+r(1920)
			left: 19%
			 
		&:before
			color: $arr
		&:hover
			background-color: white 
			&:before
				color: $btn 
		&:active
			background-color: white
			&:before
				color: $arr 	
	.slick-next
		left: 13%
		+r(1920)
			left: 22%
	.slick-disabled
		background: #838383 
		cursor: default
		&:hover
			background: #838383
			&:before
				color: $arr
		&:active
			background: #838383 
			&:before
				color: $arr
.archive
	+archive
	margin-bottom: 2.5rem
	font-size: 3.3rem
	color: white 
	+r($md)
		margin-bottom: 0
.crsl-history-nav
	background: $btn
	font-size: 0
	.slick
		visibility: hidden 
		opacity: 0
		&.is-ready
			visibility: visible
			opacity: 1
	.slick-next,
	.slick-prev
		display: none !important
	.slick-list
		margin: 0 -1.5rem 
	&__item
		color: white
		display: inline-block
		position: relative
		cursor: pointer
		+reg
		font-size: 1.5rem
		padding: 1.6rem 1.8rem 1.2rem 
		border: 3px solid transparent
		transition: border-color .3s
		&:before
			content: ""
			position: absolute
			+arr(.8,.4,white,t) 
			left: 50%
			margin-left: -0.4rem
			top: -0.5rem
			opacity: 0 
			transition: opacity .3s 
	.slick-current
		.crsl-history-nav__item
			border-color: white !important
			&:before
				opacity: 1
		
			

		