.video-slider
	position: relative
	
	&.is-ready
		.preloader
			display: none
		.slick-slider
			visibility: visible
	&__in
		overflow: hidden
		height: 100vh
		video, img
			display: block
			width: 100%
			height: auto
			+r(1200)
				width: auto
				height: 100%
			+rh(480)
				width: 100%
				height: auto
	.preloader
		position: absolute
		top: 50%
		left: 50%
		transform: translate3d(-50%,-50%,0)
	.slick-slider
		visibility: hidden
	
.post
	width: 100%
	+r($xs)
		margin-bottom: 1rem
	&__img
		width: 100%
		height: 17.2rem
		overflow: hidden
		+r($xs)
			height: 10.3rem
		img
			height: auto
			width: 100%
	&__svg
		height: 6.5rem
		margin-bottom: .5rem 
		position: relative
		+r($xs)
			margin-bottom: 0
			height: 6rem
		i
			display: inline-block
			vertical-align: bottom
			// left: 0
			// bottom: 0
			color: $btn
			+icon
	.icon-cell
		font-size: 5rem
		+r($xs)
			font-size: 4.6rem
	.icon-loc
		font-size: 6.4rem 
		+r($xs)
			font-size: 6rem 
	.icon-car
		font-size: 5.5rem 
	.icon-horn
		background-image: url(../img/horn.svg)
		background-size: 7.7rem 3.5rem
		height: 3.5rem
		width: 7.7rem 
		+r($xs)
			height: 3.3rem
			width: 7.3rem
			background-size: 7.3rem 3.3rem
	.icon-check
		font-size: 6.4rem
		+r($xs)
			font-size: 6rem
	.icon-bag
		background-image: url(../img/bag.svg)
		background-size: 7.4rem 6.4rem
		width: 7.4rem
		height: 6.4rem
		+r($xs)
			width: 7rem 
			height: 6rem
			background-size: 7rem 6rem 
	.icon-scheme
		font-size: 6.3rem
	.icon-hands
		font-size: 6.4rem
	.icon-inyan
		font-size: 6.2rem
	.icon-love
		font-size: 5.9rem
	.icon-comment
		font-size: 6.2rem
	.icon-planet
		font-size: 6.4rem
	.icon-inyan
		background-image: url(../img/inyan.svg)
		background-size: 6.4rem 6.4rem
		width: 6.4rem
		height: 6.4rem
.name
	font-size: 2rem
	+med
	color: $title
	line-height: 1
	margin: 1.7rem 0 .8rem 
	&_big
		+light
		text-transform: uppercase
		font-size: 2.4rem 
		+r($xs)
			font-size: 2.2rem
		+r($xxs)
			font-size: 2rem
	&_light
		text-transform: uppercase 
		+light
		+r($xs)
			font-size: 2.2rem
		+r($xxs)
			font-size: 1.8rem
	&.no-marg
		margin: 0 
	&.with-mb
		margin-bottom: 2.8rem 
		+r($xs)
			margin-bottom: 1.8rem		
.poster
	position: absolute
	right: 0
	left: 43%
	top: 0
	bottom: 0 
	+r($xs)
		+cover
		top: 0
		bottom: 0
		right: 0
		left: 0
		height: auto
		width: auto
		margin-left: -1rem
		margin-right: -1rem
	img
		width: 100%
		height: auto
		+r($xs)
			height: auto
			width: auto
	


.text
	&[data-column="2"]
		column-count: 2
		+r($xs)
			column-count: 1
	p
		margin-top: 1.5rem 
		&:first-child
			margin-top: 0
	&_secondary
		color: white 
	.with-mb
		margin-bottom: 3rem
		+r($xs)
			margin-bottom: 2.5rem
	.with-mt
		margin-top: 3.5rem
		+r($xs)
			margin-top: 2.5rem  
	&.with-marg
		margin: 2.2rem 0 2.7rem
		+r($xs)
			margin: 1.8rem 0 1.3rem 
		
.video
	height: 100vh
	overflow: hidden
	img
		width: 100%
		height: auto


